.container{
    text-align: center;
    text-decoration: dashed;
    color: white;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 20px;
    margin: auto;
    background: rgba(94, 89, 89, 0.539);
   width: 80%;
}
.container button{
    width: 100px;
    height: 50px;
    margin: 20px;
    background-color: rgba(112, 127, 174, 0.877);
    color: rgb(219, 146, 28);
    border-radius: 10px;
    border: solid;
}
.container button:hover{
    background-color: rgba(48, 58, 200, 0.877);
    color: rgb(234, 232, 229);
    border-radius: 30px;
    border: solid;
}